:root {
  --bg-color: #F8F8F8;
  --primary-color: #F4E041;
  --secondary-color:#00BDD3;
  --ligth-gray-color: #D0CFCF;
  --error-color: #CB3D40;
  --gray-text-color: #7E7E7E;

}

html * {
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--bg-color);
}

input:not([type="radio"]) {
  appearance: none; /* Remove platform-specific styling */
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none; /* Remove border */
  outline: none; /* Remove outline */
  background: none; /* Remove background */
  font-family: inherit; /* Inherit font styles from parent */
  font-size: inherit;
  color: inherit;
  width: 100%; /* Set the width to fill the container */
}

/* Basic CSS Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

/* Additional Reset Styles */
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

input,
textarea,
select,
button {
  outline: none;
}

*[title]:not(input) {
  pointer-events: none;
  cursor: pointer !important;
  position: relative;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  scroll-behavior: smooth;
  width: 100%;
}

.section {
  width: 100%;
  padding: 0 16px;
}

.block {
  margin-top: 140px;
}

.h1 {
  text-align: center;
  width: 100%;
  font-size: 40px;
  line-height: 40px;
}

.p1 {
  font-size: 16px;
  line-height: 26px;
}

.input-field {
  width: 380px;
  height: 54px;
}

.error {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 12px;
  line-height: 14px;
  color: var(--error-color);
  margin: 4px auto 0 16px;
  transform: translateY(1rem);
}

.hint {
  position: absolute;
  font-size: 12px;
  line-height: 14px;
  bottom: 0;
  left: 0;
  font-size: 12px;
  line-height: 14px;
  transform: translateY(1rem);
  color: var(--grey-text-color);
  margin: 4px auto 0 16px;
}

.error-field {
  border: 4px solid var(--error-color);
  color: inherit;
}


@media (max-width: 359px) {
  :root {
    font-size: 12px;
  }

  #root > * {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #root * > * {
    max-width: 90%;
  }

  #root > * > *, .error, .error-field {
    max-width: 80%;
  }

  .block {
    width: 100%;
    max-width: 90dvw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .section {
    max-width: 90dvw;
  }

  p.h1 {
    display: block;
    position: relative;
    text-align: center;
    max-width: 90%;
    font-size: 40px;
    line-height: 40px;
    height: auto;
    line-break: anywhere;
    height: max-content;
  }

  .p1.overflow {
    font-size: 16px;
    line-height: 26px;
    max-width: 50%;
  }
}

@media (min-width: 768px) {
  .section:not(.users-flex) {
    padding: 0 32px;
  }
}

@media (min-width: 1170px) {
  .section {
    padding: 0 0;
    width: 1170px;
    max-width: 1170px;
  }
}
